.transactionHistory {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  width: 300px;
  margin-top: 20px;
  margin-left: auto;
  margin-right: auto;

  border-radius: 5px;
  background-color: #fff;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
  border-collapse: collapse;
}

.thead {
  width: 100%;
  /* background-color: lightcoral; */
}

.th {
  width: 40%;
  background-color: aliceblue;
  border-radius: 5px;
}
